import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Materia } from '../../shared/Materia';

@Injectable()
export class BackProvider {

  public API: string = 'http://167.99.12.186:1337/api/v1'
  //public API: string = 'http://localhost:1337/api/v1'

  public uid: string = localStorage.getItem('uid')
  get _uid(): string { return localStorage.getItem('uid') }

  constructor(public http: HttpClient) {}

  // auth
  facebookApp(token: string, email: string) {
    return this.http.post<any>(`${this.API}/user/facebookApp`, {token, email})
  }

  googleApp(profile: any) {
    return this.http.post<any>(`${this.API}/user/googleApp`, {profile})
  }

  login(email: string, password: string) {
    return this.http.post<any>(`${this.API}/user/login`, {email, password})
  }

  purgeGoogle() {
    return this.http.get(`${this.API}/user/purgeGoogleAccounts`)
  }

  isRole(role: string) {
    const user = JSON.parse(localStorage.getItem('user'))
    if (!user) return false
    return user.roles.indexOf(role) >= 0
  }

  get isAdmin() { return this.isRole('ROLE_ADMIN') }
  get isEnarm() { return this.isRole('ROLE_ENARM') }
  get isPocket() { return this.isRole('ROLE_POCKET') }
  get isPremium() { return this.isRole('ROLE_PREMIUM') }
  get isTemprano() { return this.isRole('ROLE_TEMPRANO') }
  
  getToken(): string {
    return localStorage.getItem('token') || null 
  }

  // asesorias
  getAllAsesorias() {
    return this.http.get<any[]>(`${this.API}/asesoria/getAsesoriasHome`)
  }

  getAsesoriaDetail(id: string) {
    return this.http.get<any>(`${this.API}/asesoria/${id}`)
  }

  addAsesoria(data: any) {
    return this.http.post(`${this.API}/asesoria`, data)
  }

  // materias
  getAllMaterias() {
    return this.http.get<Materia[]>(`${this.API}/materia`)
  }

  getMateriaDetail(id: string) {
    return this.http.get<Materia>(`${this.API}/materia/${id}`)
  }

  getTemario(id: string) {
    return this.http.get<any[]>(`${this.API}/materia/getTemario/${id}`)
  }

  // tema
  getTemaDetail(id: string) {
    return this.http.get<any>(`${this.API}/tema/${id}`)
  }

  // subtema
  getSubtemaDetail(id: string) {
    return this.http.get<any>(`${this.API}/subtema/${id}`)
  }

  // clase (generalizacion de tema y subtema)
  getClase(id: string, type: string) {
    return this.http.get<any>(`${this.API}/${type}/${id}`)
  }

  updateClase(id: string, type: string, data: any) {
    return this.http.put<any[]>(`${this.API}/${type}/${id}`, data)
  }

  // comentarios
  getComentarios(id: string, type: string) {
    return this.http.get<any[]>(`${this.API}/comentario?${type}=${id}`)
  }

  addComentario(type: string, id: string, user: string, text: string) {
    // back.post('comentario/addComentario/'+this.id, {text, type: this.type, uid: localStorage.getItem('uid')})
    return this.http.post<any>(`${this.API}/Comentario/addComentario/${id}`, {text, type, uid: user})
  }

  // notas
  getNotas(id: string, type: string) {
    return this.http.get<any[]>(`${this.API}/nota?${type}=${id}&user=${this.uid}`)
  }

  getMyNotas(id: string) {
    return this.http.get<any[]>(`${this.API}/nota/getMyNotas/${id}`)
  }

  getNotasRest(id: string, rest: string, uid: string) {
    return this.http.get<any[]>(`${this.API}/nota?${rest}=${id}&user=${uid}`)
  }

  addNota(title: string, text: string, type: string, id: string) {
    return this.http.post<any>(`${this.API}/nota`, {title, text, [type]: id, user: this.uid})
  }

  // examen test
  getExamen(id: string, type: string) {
    return this.http.get<any>(`${this.API}/${type}/${id}`)
  }

  // resultados
  getResultados(type: string, user: string, id: string) {
    return this.http.get<any[]>(`${this.API}/Resultado${type}?user=${user}&${type}=${id}`)
  }

  deleteResultados(type: string, user: string, id: string) {
    return this.http.delete<any[]>(`${this.API}/Resultado${type}/${id}?user=${user}`)
  }

  addResultado(type: string, user: string, id: string, resultados: any) {
    return this.http.post<any>(`${this.API}/Resultado${type}`, {[type]: id, user, resultados})
  }

  // slides infografias
  getSlides() {
    return this.http.get<any[]>(`${this.API}/slide/getSlideList`)
  }

  getSlideDetail(id: string) {
    return this.http.get<any>(`${this.API}/slide/${id}`)
  }

  getSlideChild(id: string) {
    return this.http.get<any[]>(`${this.API}/SlideChild/${id}`)
  }

  getGroupedSlides() {
    return this.http.get<any[]>(`${this.API}/Slide/getGroupedSlides`)
  }

  // media material extra descargables
  getAllMedias() {
    return this.http.get<any[]>(`${this.API}/media`)
  }

  // galerias infografias
  getAllGalerias() {
    return this.http.get<any[]>(`${this.API}/galeria/getGaleriasHome`)
  }

  getGaleriaDetaul(id: string) {
    return this.http.get<any>(`${this.API}/galeria/${id}`)
  }

  getGroupedMedia() {
    return this.http.get<any[]>(`${this.API}/media/getGroupedMedia`)
  }

  // simuladores
  getSimuladoresHome() {
    return this.http.get<any[]>(`${this.API}/simulador/getSimuladoresHome`)
  }

  getSimuladorDetail(id: string) {
    return this.http.get<any>(`${this.API}/simulador/${id}`)
  }

  // foro
  getAllForos() {
    return this.http.get<any[]>(`${this.API}/CatHilo`)
  }

  getForoDetail(id: string) {
    return this.http.get<any>(`${this.API}/CatHilo/${id}`)
  }

  getHiloDetail(id: string) {
    return this.http.get<any>(`${this.API}/Hilo/${id}`)
  }

  getRespuestasHilo(id: string) {
    return this.http.get<any[]>(`${this.API}/RespuestaHilo?hilo=${id}`)
  }

  addRespuesta(id: string, text: string, user: string) {
    return this.http.post<any>(`${this.API}/RespuestaHilo`, {hilo: id, text, user})
  }

  // checklist todo
  getAllTodos() {
    return this.http.get<any[]>(`${this.API}/Todo`)
  }

  updateTodo(id: string, data: any) {
    return this.http.put<any[]>(`${this.API}/Todo/${id}`, data)
  }

  // calendar
  getEventosDay() {
    return this.http.get<any[]>(`${this.API}/Evento/getEventosDay`)
  }

  // notis notifications
  getNotisUser(id: string) {
    return this.http.get<any[]>(`${this.API}/Notifcation?user=${id}`)
  }

  updateNoti(id: string, data: any) {
    return this.http.put<any[]>(`${this.API}/Notifcation/${id}`, data)
  }

  // device
  registerDevice(registrationId: string, model: string, platform: string, uuid: string, version: string, manufacturer: string): Promise<any> {
    return new Promise((resolve, reject) => {
      this.http.post(`${this.API}/Device`, {registrationId, model, platform, uuid, version, manufacturer}).subscribe(data => {

      }, err => {
        
      })
    })
  }

  saveFcmToken(uid: string, token: string) {
    return new Promise((resolve, reject) => {
      this.http.put(`${this.API}/user/saveFcmToken/${uid}`, {token}).subscribe(data => resolve(data), err => reject(err))
    })
  }

  // feed
  loadFeed() {
    return this.http.get<any[]>(`${this.API}/Post`)
  }

  getPostComments(post: string) {
    return this.http.get<any[]>(`${this.API}/PostComment/getPostComments/${post}`)
  }

  addPostLike(id: string, user: string) {
    return this.http.post<any>(`${this.API}/Post/addLike`, {id, user})
  }

  removePostLike(id: string, user: string) {
    return this.http.post<any>(`${this.API}/Post/removeLike`, {id, user})
  }

}



