import { Device } from '@ionic-native/device';
import { BackProvider } from './../providers/back/back';
import { Component, ViewChild } from '@angular/core';
import { Platform, NavController, MenuController, ToastController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Push } from '@ionic-native/push';
import { FcmProvider } from '../providers/fcm/fcm';
import { tap } from '../../node_modules/rxjs/operators';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {

  @ViewChild('contenido') nav: NavController
  rootPage: any = 'LoginPage';

  constructor(
    public platform: Platform, 
    public push: Push, 
    public device: Device, 
    public back: BackProvider, 
    private menuCtrl: MenuController, 
    statusBar: StatusBar, 
    splashScreen: SplashScreen, 
    fcm: FcmProvider, 
    toast: ToastController
  ) {
    platform.ready().then(() => {

      if (!this.platform.is('cordova')) return

      fcm.getToken()

      fcm.listenToNotifications().pipe(
        tap(msg => {
          toast.create({message: msg.body, duration: 2000}).present()
        })
      ).subscribe()
      
      statusBar.styleDefault()
      splashScreen.hide()

    });
  }

  logout() {
    localStorage.removeItem('user')
    localStorage.removeItem('token')
    localStorage.removeItem('uid')
    this.nav.setRoot('LoginPage')
  }

  goto(page: string) {
    this.nav.push(page)
    this.menuCtrl.close()
  }

}



