import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { BackProvider } from '../providers/back/back';
import 'rxjs/add/operator/do';
import { NavController, ToastController } from 'ionic-angular';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {

  constructor(public auth: BackProvider) {}

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    
    request = request.clone({
      setHeaders: {
        'x-token': this.auth.getToken() || ''
      }
    })

    return next.handle(request)
    
  }
}

@Injectable()
export class JwtInterceptor implements HttpInterceptor {

  constructor(public auth: BackProvider, private navCtrl: NavController, private toast: ToastController) {}

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    
    return next.handle(request).do((event: HttpEvent<any>) => {
      if (event instanceof HttpResponse) {
        console.log(request);
        
      }
    }, (err: any) => {
      if (err instanceof HttpErrorResponse) {
        if (err.status === 401) {
          console.log(err);
          
          localStorage.removeItem('uid')
          localStorage.removeItem('user')
          localStorage.removeItem('token')
          this.toast.create({message: 'Necesitas iniciar sesin para ver ste contenido...', duration: 2000}).present()
          this.navCtrl.setRoot('LoginPage')
        }
      }
    });
  }
}



